import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: 'home',
    loadChildren: () =>
      import('./home/home.module').then((m) => m.HomePageModule),
  },
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full',
  },
  {
    path: 'batalla',
    loadChildren: () => import('./game-ring/game-ring.module').then((m) => m.GameRingPageModule),
  },
  {
    path: 'multijugador',
    loadChildren: () => import('./multiplayer-ring/multiplayer-ring.module').then( m => m.MultiplayerRingPageModule)
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      preloadingStrategy: PreloadAllModules,
      onSameUrlNavigation: 'reload',
    }),
  ],

  exports: [RouterModule],
})
export class AppRoutingModule {}
